@import url(https://fonts.googleapis.com/css2?family=Stylish&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --sidebar_background: #222831;
  --white: #ffffff;
  --body_background: #393e46;
}
* {
  padding: 0;
  margin: 0;
  font-family: sans-serif;
  color: white;
}
body {
  width: 100wh;
  text-align: center;
  height: 100vh;
  overflow: hidden;
}

.sidebar-body-container {
  display: flex;
}

.header {
  background-color: antiquewhite;
  width: 100wh;
  font-size: 3rem;
  text-align: left;
  display: flex;
}
.main-container {
  display: flex;
  flex-direction: column;
}

.quick-sort-pivot-body {
  background-color: rgba(0, 119, 99, 0.459);
  padding: 10px;
  border-radius: 3px;
}

.quick-sort-pivot-heading {
  margin-bottom: 10px;
}

.quick-sort-pivot-checkbox-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 0.3fr 0.3fr;
  /* gap: 0px 0px; */
  /* grid-auto-flow: row; */
  text-align: left;
}

.quick-sort-pivot-checkbox {
  text-align: left;
}

.body-main-container {
  display: flex;
  flex-wrap: wrap;
  background-color: var(--body_background);
  flex-basis: 70%;
  justify-content: center;
  align-items: flex-end;
  position: relative;
}
.body-controller-container {
  position: absolute;
  top: 0;
  left: 0;
}

.bars-container {
  margin-top: 10vh;
}
.container {
  position: relative;
  width: 600px;
  margin: auto;
  bottom: 5vh;
  display: flex;
}
.bar {
  height: 100px;
  background-color: rgb(0, 225, 255);
  font-size: 2rem;
  font-weight: bold;
  border-radius: 3px 3px 0 0;
  position: absolute;
  border: 1px solid black;
  bottom: 0;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  color: black;
}
.barcount {
  color: black;
}
.bar:hover .barcount {
  font-size: 2rem !important;
  color: white;
  transform: translateY(2rem);
}

.blue {
  background-color: rgb(0, 174, 255);
}

.green {
  background-color: green;
}

.red {
  background-color: #ff0000;
}

.yellow {
  background-color: rgb(255, 165, 0);
}

.purple {
  background-color: purple;
}

.generate-new-array-btn-container {
  margin: 50px auto;
}

.array-input-container {
  position: relative;
}

.array-input {
  background-color: white;
  color: black;
  font-size: 20px;
  width: 80%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  outline: none;
  padding: 7px 12px;
  border: none;
  border-radius: 4px;
  margin-top: 20px;
  max-width: 90%;
}
.input-array-btn {
  margin: 5px auto !important;
}
.array-input-label {
  margin-right: auto;
  position: absolute;
}

.sidebar-container {
  flex-basis: 30%;
}

.controller-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex-basis: 30%;
  background-color: var(--sidebar_background);
  min-height: 90vh;
  border-top: 1px solid var(--body_background);
  padding-top: 20px;
}

.sliders-container {
  margin: 5% auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.github-logo-container {
  display: flex;
  flex-direction: column;
  margin: 10px auto;
  align-items: center;
}

.header-container {
  min-height: 30px;
  padding: 10px;
  background-color: #222831;
}
.header-heading-container {
  text-align: center;
}
.header-heading {
  font-family: "Stylish", sans-serif;
  font-size: 2rem;
}

